import React from "react";
import fetch from "isomorphic-unfetch";
import "../styles/reset.scss";
import "../styles/global.scss";
import style from "../styles/layout.scss";

import Head from "../components/head";
import Nav from "../components/nav";
import Schedule from "../components/schedule";
import Data from "../data";

class Index extends React.Component {
  constructor(props) {
    super(props);
    this.handleBoxIdChange = this.handleBoxIdChange.bind(this);
    this.state = {
      loading: true,
      // data: {},
      // boxId: null,
      // currentCity: null,
    };
  }

  handleBoxIdChange(boxId) {
    this.setState({ boxId });
  }

  componentDidMount() {
    let boxId = localStorage.getItem("boxId");
    const currentCity = localStorage.getItem("currentCity") || "北京市";
    const { cityMap } = Data.data.main.boxArea;
    const { boxList } = cityMap[currentCity][0];

    if (boxList.every(item => item.boxId !== boxId)) {
      boxId = boxList[0].boxId;
    }

    // this.setState({
    //   loading: false,
    //   data: Data.data,
    //   boxId,
    //   currentCity,
    // });
  }

  async fetch(city) {
    this.setState({ loading: true });
    const result = await fetch(
      `https://busi.supermonkey.com.cn/wxClass/getClassSelectList3?city=${encodeURI(
        city
      )}`
    );
    const json = await result.json();
    console.log(json.data);
    // return { data: json.data }
  }

  render() {
    // const { data } = this.props;
    const { loading } = this.state;
    if (loading) return <div></div>;
    const { data, boxId, currentCity } = this.state;

    return (
      <div className={style.main}>
        <Head
          title="超级猩猩·课程表"
          description="超级猩猩健身在线课程表"
          url="https://super.kshift.me"
        />
        <Nav
          data={data}
          boxId={boxId}
          currentCity={currentCity}
          onBoxIdChange={this.handleBoxIdChange}
        />
        <Schedule data={data} boxId={boxId} />
      </div>
    );
  }
}

export default Index;
